
.gradient-text {
	text-align: center;
    font-size:2.5rem;
    background-color: #f3ec78;
    background-image: linear-gradient(90deg, #f3ec78, #af4261);
    background-size: 100%;
   -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
    animation: rainbow-text-simple-animation-rev 0.75s ease forwards;
  }

.container
{
	width:70%;
	height:70%;
	margin: 1px;
	position:absolute;
	top:13%;
	left:15%;
	bottom:2%;
	display:flex;
	flex-wrap: wrap;
	flex-direction: column;
    justify-content: space-between;
    overflow:auto;
}


.wrap
{
display:flex;
width:25%;
height:25%;
position:absolute;
flex-wrap: wrap;
flex-direction: row;
margin:2px;
}

.first
{

top:1%;
left:0%;


}


.second
{

top:1%;
left:25%;

}

.third
{

top:1%;
left:50%;
}

.fourth
{
top:1%;
left:75%;
}


.eachimg
{
    width:8px;
	height:8px;
	margin:3px;
	border-radius: 50%;
    transition: transform .2s;
}

.eachimg:hover {
  -ms-transform: scale(1.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(5); 
}

